@use "./colors.scss" as *;

body {
  width: 100%;
  height: 100vh;

  /* header styling */
  header {
    position: absolute;
    min-height: 100vh;

    & .header__container {
      padding: 3.2rem 2.5rem 0 2.5rem;

      & nav.header__nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        width: 33rem;

        & img {
          display: block;
          width: 13.5rem;
          height: 2.5rem;
        }

        & button#times {
          display: none;
        }

        & ul.nav__nav-list {
          display: flex;
          flex-direction: column;
          gap: 2.1rem;
          width: 100%;
          margin-top: 16rem;
          font-size: 2.5rem;
          opacity: 0.7;
          display: none;
        }

        & ul.nav__nav-list.show {
          display: flex;
        }
      }
    }
  }

  /* showcase styling */
  section.showcase {
    padding: 22rem 2rem 0 2rem;
    min-height: 100vh;
    background-image: url(/images/mobile/image-hero.jpg);
    background-repeat: no-repeat;
    background-size: contain;

    & .showcase__content {
      padding: 2.3rem 3rem 1.7rem 2.5rem;
      font-size: 2.6rem;
      text-transform: uppercase;
      letter-spacing: 0.05rem;
      border: 0.1995rem solid $primary-white;
    }
  }

  main.main {
    margin: 7rem 2rem 0 2rem;
    /* interactive VR styling */
    & section.main__interactive-VR {
      display: flex;
      flex-direction: column;

      & .interactive-VR__image img {
        display: block;
        width: 33.5rem;
        height: 22.2rem;

        &.desktop-interactive-image {
          display: none;
        }
      }

      & .interactive-VR__content {
        margin-top: 4.5rem;
        padding: 0 3rem;
        text-align: center;

        & h1 {
          font-size: 3.1rem;
          text-transform: uppercase;
          color: $primary-black;
        }

        & p {
          margin-top: 2.8rem;
          font-size: 1.5rem;
          line-height: 1.6;
          color: $primary-verydarkgray;
        }
      }
    }

    /* our creations styling */
    & section.main__our-creations {
      margin-top: 9.6rem;
      text-transform: uppercase;

      & h1.our-creations__heading {
        font-size: 3rem;
        text-align: center;
        color: $primary-black;
      }

      & .our-creations__gallery {
        margin-top: 5rem;
        display: grid;
        grid-template-columns: minmax(32rem, 1fr);
        grid-template-rows: repeat(8, minmax(11.5rem, 1fr));
        gap: 2rem;

        & .gallery__card {
            padding: 4.9rem 0 0 1.7rem;
            background-repeat: no-repeat;
            background-size: 100%;
            box-shadow: 168px 0px 21px 0px rgba(0,0,0,0.47) inset;
            cursor: pointer;
          &:nth-of-type(1) {
            background-image: url(/images/mobile/image-deep-earth.jpg);
          }
          &:nth-of-type(2) {
            background-image: url(/images/mobile/image-night-arcade.jpg);
          }
          &:nth-of-type(3) {
            background-image: url(/images/mobile/image-soccer-team.jpg);
          }
          &:nth-of-type(4) {
            background-image: url(/images/mobile/image-grid.jpg);
          }
          &:nth-of-type(5) {
            background-image: url(/images/mobile/image-from-above.jpg);
          }
          &:nth-of-type(6) {
            background-image: url(/images/mobile/image-pocket-borealis.jpg);
          }
          &:nth-of-type(7) {
            background-image: url(/images/mobile/image-curiosity.jpg);
          }
          &:nth-of-type(8) {
            background-image: url(/images/mobile/image-fisheye.jpg);
          }

          & h3.card__title {
              font-size: 2.3rem;
          }
        }
      }

      & .our-creations__button {
         & a button {
             display: block;
             margin: 3rem 0 0 8rem;
             padding: 1rem 4.5rem;
             border: 0.199rem solid $primary-black;
             font-family: "Alata";
             font-size: 1.3rem;
             letter-spacing: 0.4rem;
             text-transform: uppercase;
             color: $primary-black;
         }
      }
    }
  }
}
